@import "./../../vars.scss";
.wrapper-directions {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__background {
        background: url(../../assets/Images/HotImages/mountsBig.png) no-repeat;
        background-size: cover;
        // height: 100%;
        min-height: 500px;
        width: 100%;
        background-position: inherit;
        @media (max-width: $desktop) {
            min-height: 556px;
        }
        @media (max-width: $netbook) {
            min-height: 476px;
        }
        @media (max-width: $phone) {
            min-height: 400px;
        }
    }
    &__title {
        margin: 0 auto;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding-top: 105px;
    }
    &__text {
        font-size: 16px;
        font-family: "Open Sans", sans-serif;
        font-weight: 400;
        line-height: 2;
        color: #111111;
        @media (max-width: $netbook) {
            min-height: 400px;
        }
        @media (max-width: $phone) {
            font-size: 10px;
            min-height: 200px;
        }
    }
    &__cards {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-content: space-between;
        padding: 35px;
        background-color: #ffffff;
        margin-top: -160px;
        @media (max-width: $desktop) {
            width: 500px;
            padding: 10px;
            margin-top: -200px;
        }
        @media (max-width: $netbook) {
            width: 500px;
            padding: 10px;
        }
        @media (max-width: $phone) {
            width: 350px;
        }
    }
    &__button {
        padding: 32px 0 58px;
    }
}