@import "../../vars.scss";
h1 {
    font-family: 'Open Sans', sans-serif;
    line-height: 1.1;
    font-weight: 400;
    font-size: 96px;
    padding-bottom: 30px;
    padding-top: 20px;
}

h2 {
    // color: #ff5126;
    font-family: Roboto, sans-serif;
    font-weight: 400;
    text-transform: capitalize;
    line-height: 1.2;
    font-size: 72px;
    @media (max-width: $desktop) {
        font-size: 60px;
    }
    @media (max-width: $netbook) {
        font-size: 50px;
    }
    @media (max-width: $tablet) {
        font-size: 30px;
    }
    @media (max-width: $phone) {
        font-size: 20px;
    }
}

h3 {
    font-family: Roboto, sans-serif;
    font-weight: 300;
    text-transform: capitalize;
    font-size: 24px;
}

h4 {
    font-family: Roboto, sans-serif;
    font-weight: 300;
    font-size: 20px;
}

h5 {
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    font-size: 20px;
}

hr {
    height: 1px;
    background: #d9d9d9;
    width: 1140px;
}

.border {
    border: 1px solid;
    border: 3px solid rgba(0, 0, 0, 0.2);
    margin: 10px;
}

.orange {
    color: #ff5126;
}

.yellow {
    color: #c4c015;
}